}

html {
  font-size: 100%;
  font-family: "Cabin", Sans-Serif;
  color: purple;
}

/*16px*/
section {
  padding: 50px 0;
  width: 100%;
  height: 100%;
  border: 1px solid green;
}

body {
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
}

.container {
  width: 100%;
  height: 100%;
  border: 1px solid white;
  display: flex;
  flex-direction: column;
}

#main {
  width: 100%;
  height: 90%;
  display: flex;
  justify-content: center;
  align-items: center;
}
#main > * {
  border: 1px solid purple;
}

h1 {
  color: purple;
}

header {
  padding: 15px 0;
  background-color: chartreuse;
}
header nav ul.nav__links {
  display: flex;
  list-style-type: none;
  justify-content: space-evenly;
  text-transform: uppercase;
}

form {
  padding: 100px 50px;
}